<template>
  <view class="ranking-page">
    <!-- ÊêúÁ¥¢Ê†è -->
    <view class="search-section">
      <view class="search-container">
        <view class="search-icon">üîç</view>
        <input 
          class="search-input" 
          type="text" 
          placeholder="ÊêúÁ¥¢"
          v-model="searchKeyword"
          @input="onSearchInput"
        />
      </view>
    </view>
    
    <!-- Á¨¨‰∏ÄÂ±ÇÁ∫ßÔºöÂàÜÁ´ôÈÄâÊã© -->
    <view class="sites-section">
      <scroll-view class="sites-scroll" scroll-x>
        <view class="sites-container">
          <view 
            class="site-tag"
            :class="{ active: selectedSite && selectedSite.id === site.id }"
            v-for="site in sites" 
            :key="site.id"
            @tap="selectSite(site)"
          >
            <text class="site-text">{{ site.name }}</text>
          </view>
        </view>
      </scroll-view>
    </view>
    
    <!-- Á¨¨‰∫åÂ±ÇÁ∫ßÔºöÈ¢ëÈÅìÈÄâÊã©Ôºà‰ªÖÂ§çÊùÇÂàÜÁ´ôÊòæÁ§∫Ôºâ -->
    <view v-if="selectedSite && selectedSite.type === 'complex'" class="channels-section">
      <scroll-view class="channels-scroll" scroll-x>
        <view class="channels-container">
          <view 
            class="channel-tag"
            :class="{ active: selectedChannel && selectedChannel.id === channel.id }"
            v-for="channel in currentChannels" 
            :key="channel.id"
            @tap="selectChannel(channel)"
          >
            <text class="channel-text">{{ channel.name }}</text>
          </view>
        </view>
      </scroll-view>
    </view>
    
    <!-- Á¨¨‰∏âÂ±ÇÁ∫ßÔºöÂÜÖÂÆπÊòæÁ§∫ -->
    <view class="content-section">
      <!-- Â§πÂ≠êÔºöÊòæÁ§∫‰π¶Á±çÂàóË°® -->
      <view v-if="selectedSite && selectedSite.id === 'jj' && level === 3" class="books-section">
        <BookList 
          :books="books"
          :title="currentRankingTitle"
          :show-count="true"
          :show-rank="true"
          :show-actions="false"
          @book-tap="handleBookTap"
        />
      </view>
      
      <!-- ÂÖ∂‰ªñÂàÜÁ´ôÔºöÊòæÁ§∫Ê¶úÂçïÂàóË°® -->
      <view v-else-if="selectedSite && level >= 2" class="rankings-section">
        <view class="rankings-list">
          <view 
            class="ranking-item" 
            v-for="ranking in currentRankings" 
            :key="ranking.id"
            @tap="goToRankingDetail(ranking)"
          >
            <text class="ranking-name">{{ ranking.name }}</text>
          </view>
        </view>
      </view>
    </view>
  </view>
</template>

<script>
import { getSitesList, getSiteById, getChannelsBySiteId } from '@/data/url.js'
import BookList from '@/components/BookList.vue'

/**
 * Ê¶úÂçïÈ°µÈù¢
 * @description Â§öÂ±ÇÁ∫ßÂØºËà™Â±ïÁ§∫Ê¶úÂçïÂíå‰π¶Á±ç‰ø°ÊÅØÔºåÊåâÁÖßFigmaËÆæËÆ°Ê†∑Âºè
 */
export default {
  name: 'RankingPage',
  
  components: {
    BookList
  },
  
  data() {
    return {
      searchKeyword: '',
      sites: [],
      selectedSite: null,
      selectedChannel: null,
      currentChannels: [],
      currentRankings: [],
      books: [],
      level: 1, // 1: ÂàÜÁ´ôÈÄâÊã©, 2: È¢ëÈÅìÈÄâÊã©, 3: ÂÜÖÂÆπÊòæÁ§∫
      currentRankingTitle: ''
    }
  },
  
  onLoad(options) {
    this.initData()
    
    // Â§ÑÁêÜÂ§ñÈÉ®‰º†ÂÖ•ÁöÑÂèÇÊï∞
    if (options.site) {
      const site = getSiteById(options.site)
      if (site) {
        this.selectSite(site)
      }
    } else {
      // Ê≤°ÊúâÂ§ñÈÉ®ÂèÇÊï∞Êó∂ÔºåÂ∞ùËØïÊÅ¢Â§çÂéÜÂè≤ÈÄâÊã©
      this.restoreLastSelection()
    }
  },
  
  methods: {
    /**
     * ÂàùÂßãÂåñÊï∞ÊçÆ
     */
    initData() {
      try {
        this.sites = getSitesList()
        console.log('Âä†ËΩΩÂàÜÁ´ôÂàóË°®:', this.sites)
      } catch (error) {
        console.error('Âä†ËΩΩÂàÜÁ´ôÊï∞ÊçÆÂ§±Ë¥•:', error)
        // Êèê‰æõÂ§áÁî®Êï∞ÊçÆ
        this.sites = [
          { id: 'jj', name: 'Â§πÂ≠ê', type: 'special' },
          { id: 'shu', name: '‰π¶Âüé', type: 'simple' },
          { id: 'yan', name: 'Ë®ÄÊÉÖ', type: 'complex' }
        ]
      }
    },
    
    /**
     * ÊÅ¢Â§ç‰∏äÊ¨°ÈÄâÊã©ÁöÑtab
     */
    restoreLastSelection() {
      try {
        const lastSelection = uni.getStorageSync('ranking_last_selection')
        if (lastSelection && lastSelection.siteId) {
          console.log('ÊÅ¢Â§çÂéÜÂè≤ÈÄâÊã©:', lastSelection)
          const site = getSiteById(lastSelection.siteId)
          if (site) {
            this.selectSite(site, false) // ‰∏ç‰øùÂ≠òÂéÜÂè≤ÔºåÈÅøÂÖçÈáçÂ§ç‰øùÂ≠ò
            
            // Â¶ÇÊûúÊúâÈ¢ëÈÅìÈÄâÊã©ÂéÜÂè≤Ôºå‰πüÊÅ¢Â§ç
            if (lastSelection.channelId && site.type === 'complex') {
              const channels = getChannelsBySiteId(site.id)
              const channel = channels.find(ch => ch.id === lastSelection.channelId)
              if (channel) {
                setTimeout(() => {
                  this.selectChannel(channel, false)
                }, 100)
              }
            }
            return
          }
        }
      } catch (error) {
        console.error('ÊÅ¢Â§çÂéÜÂè≤ÈÄâÊã©Â§±Ë¥•:', error)
      }
      
      // Ê≤°ÊúâÂéÜÂè≤‰ø°ÊÅØÊàñÊÅ¢Â§çÂ§±Ë¥•ÔºåÈªòËÆ§ÈÄâ‰∏≠Â§πÂ≠ê
      const jiaziSite = this.sites.find(site => site.id === 'jj')
      if (jiaziSite) {
        this.selectSite(jiaziSite)
      }
    },

    /**
     * ‰øùÂ≠òÂΩìÂâçÈÄâÊã©Âà∞ÂéÜÂè≤
     */
    saveCurrentSelection() {
      try {
        const selection = {
          siteId: this.selectedSite?.id,
          channelId: this.selectedChannel?.id,
          timestamp: Date.now()
        }
        uni.setStorageSync('ranking_last_selection', selection)
        console.log('‰øùÂ≠òÈÄâÊã©ÂéÜÂè≤:', selection)
      } catch (error) {
        console.error('‰øùÂ≠òÈÄâÊã©ÂéÜÂè≤Â§±Ë¥•:', error)
      }
    },

    /**
     * ÈÄâÊã©ÂàÜÁ´ô
     */
    selectSite(site, saveHistory = true) {
      this.selectedSite = site
      this.selectedChannel = null
      this.level = 2
      
      console.log('ÈÄâÊã©ÂàÜÁ´ô:', site)
      
      // ‰øùÂ≠òÈÄâÊã©ÂéÜÂè≤
      if (saveHistory) {
        this.saveCurrentSelection()
      }
      
      if (site.type === 'special' && site.id === 'jj') {
        // Â§πÂ≠êÔºöÁõ¥Êé•ÊòæÁ§∫‰π¶Á±çÂàóË°®
        this.level = 3
        this.currentRankingTitle = 'Â§πÂ≠êÊ¶úÂçï'
        this.loadJiaziBooks()
      } else if (site.type === 'complex') {
        // Â§çÊùÇÂàÜÁ´ôÔºöÊòæÁ§∫ÂàÜÁ´ôÊ¶úÂçï + È¢ëÈÅìÈÄâÊã©
        this.currentChannels = getChannelsBySiteId(site.id)
        this.loadSiteRankings(site)
      } else {
        // ÁÆÄÂçïÂàÜÁ´ôÔºöÁõ¥Êé•ÊòæÁ§∫Ê¶úÂçï
        this.loadSiteRankings(site)
      }
    },
    
    /**
     * ÈÄâÊã©È¢ëÈÅì
     */
    selectChannel(channel, saveHistory = true) {
      this.selectedChannel = channel
      this.level = 3
      console.log('ÈÄâÊã©È¢ëÈÅì:', channel)
      
      // ‰øùÂ≠òÈÄâÊã©ÂéÜÂè≤
      if (saveHistory) {
        this.saveCurrentSelection()
      }
      
      this.loadChannelRankings(this.selectedSite, channel)
    },
    
    /**
     * Âä†ËΩΩÂàÜÁ´ôÊ¶úÂçï
     */
    loadSiteRankings(site) {
      // Ê®°ÊãüÂàÜÁ´ôÊ¶úÂçïÊï∞ÊçÆ
      const siteRankings = {
        jj: [
          { id: 'jj_main', name: 'Â§πÂ≠êÊÄªÊ¶ú', type: 'books' },
          { id: 'jj_rising', name: 'Â§πÂ≠êÊñ∞ÊòüÊ¶ú', type: 'books' },
          { id: 'jj_hot', name: 'Â§πÂ≠êÁÉ≠Èó®Ê¶ú', type: 'books' }
        ],
        shu: [
          { id: 'shu_hot', name: 'ÁÉ≠Èó®Ê¶ú' },
          { id: 'shu_new', name: 'Êñ∞‰π¶Ê¶ú' },
          { id: 'shu_finish', name: 'ÂÆåÁªìÊ¶ú' }
        ],
        yan: [
          { id: 'yan_monthly', name: 'ÊúàÊ¶ú' },
          { id: 'yan_weekly', name: 'Âë®Ê¶ú' },
          { id: 'yan_daily', name: 'Êó•Ê¶ú' }
        ],
        chun: [
          { id: 'chun_popular', name: '‰∫∫Ê∞îÊ¶ú' },
          { id: 'chun_recommend', name: 'Êé®ËçêÊ¶ú' }
        ]
      }
      
      this.currentRankings = siteRankings[site.id] || [
        { id: `${site.id}_default`, name: 'ÈªòËÆ§Ê¶úÂçï' }
      ]
    },
    
    /**
     * Âä†ËΩΩÈ¢ëÈÅìÊ¶úÂçï
     */
    loadChannelRankings(site, channel) {
      // Ê®°ÊãüÈ¢ëÈÅìÊ¶úÂçïÊï∞ÊçÆ
      this.currentRankings = [
        { id: `${site.id}_${channel.id}_hot`, name: `${channel.name}ÁÉ≠Èó®Ê¶ú` },
        { id: `${site.id}_${channel.id}_new`, name: `${channel.name}Êñ∞‰ΩúÊ¶ú` }
      ]
    },
    
    /**
     * Âä†ËΩΩÂ§πÂ≠ê‰π¶Á±çÂàóË°®
     */
    loadJiaziBooks() {
      // Ê®°ÊãüÂ§πÂ≠ê‰π¶Á±çÊï∞ÊçÆ
      this.books = Array.from({ length: 50 }, (_, index) => ({
        id: `book_${index + 1}`,
        title: `ÈáçÁîü‰πãÂÜúÂ•≥${index + 1}`,
        collections: 193 + Math.floor(Math.random() * 1000),
        collectionChange: Math.floor(Math.random() * 100) - 50,
        rankChange: Math.floor(Math.random() * 10) - 5
      }))
    },
    
    /**
     * ÊêúÁ¥¢ËæìÂÖ•
     */
    onSearchInput(e) {
      console.log('ÊêúÁ¥¢:', e.detail.value)
      // ËøôÈáåÂèØ‰ª•ÂÆûÁé∞ÊêúÁ¥¢ÈÄªËæë
    },
    
    /**
     * Ë∑≥ËΩ¨Âà∞Ê¶úÂçïËØ¶ÊÉÖ
     */
    goToRankingDetail(ranking) {
      // Â¶ÇÊûúÊòØÂ§πÂ≠êÊ¶úÂçïÔºåÁõ¥Êé•Âú®ÂΩìÂâçÈ°µÈù¢ÊòæÁ§∫‰π¶Á±çÂàóË°®
      if (ranking.type === 'books') {
        this.level = 3
        this.currentRankingTitle = ranking.name
        this.loadJiaziBooks()
        return
      }
      
      // ÂÖ∂‰ªñÊ¶úÂçïË∑≥ËΩ¨Âà∞ËØ¶ÊÉÖÈ°µ
      uni.navigateTo({
        url: `/pages/ranking/detail?id=${ranking.id}&name=${encodeURIComponent(ranking.name)}`
      })
    },
    
    /**
     * Â§ÑÁêÜ‰π¶Á±çÁÇπÂáªÔºàBookListÁªÑ‰ª∂‰∫ã‰ª∂Ôºâ
     */
    handleBookTap({ book, index }) {
      this.goToBookDetail(book)
    },

    /**
     * Ë∑≥ËΩ¨Âà∞‰π¶Á±çËØ¶ÊÉÖ
     */
    goToBookDetail(book) {
      uni.navigateTo({
        url: `/pages/book/detail?id=${book.id}`
      })
    }
  }
}
</script>

